import ReactMarkdown from "react-markdown";
import React, { useRef, useEffect, useState } from 'react';
import html2canvas from 'html2canvas';
import ReconnectingStatus from './ReconnectingStatus';


const ANALYSIS_TYPES = [ 
  { id: 'health', name: '健康分析', icon: '❤️', color: '#ED7D31' },
  { id: 'career', name: '事业分析', icon: '💼', color: '#005293' },
  { id: 'wealth', name: '财运分析', icon: '💰', color: '#8064A6' },
  { id: 'marriage', name: '婚姻分析', icon: '💍', color: '#A3CE27' }
];

const AnalysisSection = ({ preview, unlocked, contents, onUnlock, onGoBack, onComplete }) => {
  const [displayContent, setDisplayContent] = useState('');
  const [blurContent, setBlurContent] = useState('');
  const [selectedType, setSelectedType] = useState(null);
  const [loadingType, setLoadingType] = useState(null);
  const [isReconnecting, setIsReconnecting] = useState(false);
  const [reconnectingType, setReconnectingType] = useState(null);
  const contentRef = useRef(null);
 
  const [isShared, setIsShared] = useState(false);
  const [isCopied, setIsCopied] = useState(false);

  // 修改分享处理函数
  const handleShare = async () => {
    try {
      // 先执行复制操作
      await navigator.clipboard.writeText(window.location.href);
      setIsCopied(true);
      
      // 显示提示2秒后弹出确认对话框
      setTimeout(async () => {
        const hasShared = await window.confirm(
          '链接已复制！\n\n请将链接分享到社交平台\n是否已完成分享？'
        );
        if (hasShared) {
          setIsShared(true);
        }
      }, 1000);
  
      // 隐藏复制成功提示
      setTimeout(() => setIsCopied(false), 3000);
    } catch (err) {
      alert('请手动复制地址栏链接');
      setTimeout(async () => {
        const hasShared = await window.confirm(
          '请将链接分享到社交平台\n是否已完成分享？'
        );
        if (hasShared) {
          setIsShared(true);
        }
      }, 1000);
  
    }
  };
  
  const watermarkImage = new Image();
  watermarkImage.src = '/qrcode.png'; // 准备二维码图片
  
  const handleScreenshot = async () => {
    try {
      // 第一步：捕获原始内容
      const canvas = await html2canvas(contentRef.current, {
        useCORS: true,
        allowTaint: true,
        logging: false,
      });
  
      // 第二步：创建水印画布
      const watermarkCanvas = document.createElement('canvas');
      const ctx = watermarkCanvas.getContext('2d');
      
      // 设置画布尺寸
      watermarkCanvas.width = canvas.width;
      watermarkCanvas.height = canvas.height;
  
      // 第三步：绘制原始内容
      ctx.drawImage(canvas, 0, 0);
      
      // === 新增：添加文字抬头 ===
      ctx.fillStyle = '#FF0000'; // 文字颜色
      ctx.font = 'bold 48px Microsoft YaHei'; // 字体样式
      ctx.textAlign = 'center'; // 水平居中
      ctx.textBaseline = 'top'; // 垂直对齐从顶部开始
      ctx.fillText('🌟 本报告由深智AI命理引擎生成 | www.mingli.xin', canvas.width / 2, 30);

      // 二维码水印（右下角）
      ctx.globalAlpha = 0.8;
      ctx.drawImage(
        watermarkImage,
        canvas.width - 300, // x坐标不变，右边距150
        100, // y坐标改为30，顶部边距30
        284,
        318
        );
  
      // 第五步：生成最终图片
      const finalUrl = watermarkCanvas.toDataURL('image/png');
  
      // 第六步：触发下载
      const link = document.createElement('a');
      link.href = finalUrl;
      link.download = `命理分析报告_${new Date().toLocaleDateString()}.png`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
  
    } catch (error) {
      console.error('截图失败:', error);
      alert('生成报告失败，请稍后重试');
    }
  };
  
  // 内容分割逻辑优化
  useEffect(() => {
    if (typeof preview === 'string' && preview.length > 0) {
      const splitIndex = Math.floor(preview.length * 0.9);
      setDisplayContent(preview.slice(0, splitIndex));
      setBlurContent(preview.slice(splitIndex));
    }
  }, [preview]);

  // 检查每种分析类型是否正在恢复
  useEffect(() => {
    ANALYSIS_TYPES.forEach(type => {
      const analysisId = localStorage.getItem(`paid_analysis_${type.id}`);
      // 如果有存储的分析ID但内容还没有加载，显示重连状态
      if (analysisId && !contents[type.id] && !unlocked[type.id]) {
        setIsReconnecting(true);
        setReconnectingType(type.name);
        return;
      }
    });
    
    // 如果所有内容都已加载或没有存储的分析ID，隐藏重连状态
    if (isReconnecting) {
      const allLoaded = ANALYSIS_TYPES.every(type => {
        const analysisId = localStorage.getItem(`paid_analysis_${type.id}`);
        return !analysisId || contents[type.id];
      });
      
      if (allLoaded) {
        setIsReconnecting(false);
        setReconnectingType(null);
      }
    }
  }, [contents, unlocked, isReconnecting]);

  // 处理模块解锁
  const handleUnlock = async (typeId) => {
    if (unlocked[typeId] || loadingType) return;
    
    try {
      setLoadingType(typeId);
      // 如果有存储的分析ID，设置重连状态
      const analysisId = localStorage.getItem(`paid_analysis_${typeId}`);
      if (analysisId) {
        setIsReconnecting(true);
        setReconnectingType(ANALYSIS_TYPES.find(t => t.id === typeId)?.name || '分析');
      }
      await onUnlock(typeId);
      setSelectedType(typeId); // 解锁后自动选中
    } catch (error) {
      console.error('解锁失败:', error);
    } finally {
      setLoadingType(null);
      // 如果内容已加载，隐藏重连状态
      if (contents[typeId]) {
        setIsReconnecting(false);
        setReconnectingType(null);
      }
    }
  };

  return (
    <div className="analysis-container" ref={contentRef}>
      {/* 显示重连状态提示 */}
      {isReconnecting && reconnectingType && (
        <ReconnectingStatus analysisType={reconnectingType} />
      )}

      <h2>基础命理分析</h2>

      {!onComplete ? (
        <ReactMarkdown>{preview}</ReactMarkdown>
      ) : (
        <div className="preview-section">
          <ReactMarkdown>{displayContent}</ReactMarkdown>
          {blurContent && (
            <div className={`blur-section ${(selectedType || isShared) ? 'unlocked' : 'locked'}`}>
              <ReactMarkdown>{blurContent}</ReactMarkdown>
            </div>
          )}
        </div>
      )}

      {onComplete && (
        <>
          <div className="share-section">
            {!(selectedType || isShared) && (
              <div className="share-container">
                <button 
                  onClick={handleShare}
                  className="share-trigger"
                >
                  🔗 复制链接分享解锁
                </button>
                <span className="tooltip">分享给微信好友即可查看完整解析</span>
              </div>
            )}
            
            {isCopied && (
              <div className="copied-notice">
                ✅ 链接已复制到剪贴板！
              </div>
            )}
          </div>
          
          {/* 分析模块布局 */}  
          <div className="analysis-modules">
            {ANALYSIS_TYPES.map((type) => (
              <div
                key={type.id}
                className={`analysis-module ${
                  unlocked[type.id] ? 'unlocked' : 'locked'
                } ${selectedType === type.id ? 'active' : ''}`}
                style={{ backgroundColor: type.color }}
                onClick={() => {
                  if (unlocked[type.id]) {
                    setSelectedType(type.id);
                  } else {
                    handleUnlock(type.id);
                  }
                }}
              >
                <div className="module-header">
                  <span className="icon">{type.icon}</span>
                  <h3>{type.name}</h3>
                </div>  
                <div className="module-status">
                  {loadingType === type.id ? (
                    <div className="loader" />
                  ) : unlocked[type.id] ? (
                    contents[type.id] ? '✓ 已解锁' : <div className="loader" />
                  ) : (
                    localStorage.getItem(`paid_analysis_${type.id}`) ? 
                    '正在恢复...' : 
                    '￥1.99解锁'
                  )}
                </div>
              </div>
            ))}
          </div>

          {/* 详细分析内容展示 */}
          {selectedType && unlocked[selectedType] && (
            <div className="detail-content">
              <ReactMarkdown>{contents[selectedType]}</ReactMarkdown>
            </div>
          )}
        </>
      )}
      <button className="action-button back" onClick={onGoBack}>
        ← 返回主页
      </button>
      <button className="action-button screenshot" onClick={handleScreenshot}>
        📸 截图保存
      </button>

      <div className="disclaimer">
        <p>※ 本分析由AI生成，仅供娱乐参考，不作为决策依据</p>
        <p>※ 根据相关法规，所有数据将在24小时后自动删除</p>
      </div>
    </div>
  );
};

export default AnalysisSection;
