const BASE_URL ="http://localhost:5000"

// 为付费分析添加自动恢复功能的EventSource
const createEventSourceWithRecovery = (url, handlers, analysisType = null) => {
  // 如果是付费分析且有分析类型，添加恢复标记
  if (analysisType) {
    // 检查本地存储中是否有该类型的分析ID
    const analysisId = localStorage.getItem(`paid_analysis_${analysisType}`);
    if (analysisId) {
      // 如果有，添加到URL中
      const separator = url.includes('?') ? '&' : '?';
      url = `${url}${separator}recovery=true&analysisId=${analysisId}`;
      console.log(`尝试恢复付费内容: ${analysisType}, ID: ${analysisId}`);
    }
  }
  
  const es = new EventSource(url);
  
  es.onopen = () => {
    console.log('SSE连接已建立');
  };

  es.onerror = (err) => {
    console.error('SSE连接错误:', err);
    handlers.onError?.('连接已断开,请刷新页面重试');
    es.close();
  };

  return es;
};

// 创建八字分析SSE流
export const createOrderStream = (eightCharacters, handlers) => {
  const url = `${BASE_URL}/api/orders/stream?eightCharacters=${encodeURIComponent(eightCharacters)}`;
  const es = createEventSourceWithRecovery(url, handlers);

  // 监听不同类型的事件
  es.addEventListener('stream', (e) => {
    const data = JSON.parse(e.data);
    handlers.onStream?.(data.content); // 流式传输内容
  });

  es.addEventListener('stream_end', (e) => {
    const data = JSON.parse(e.data);
    handlers.onComplete?.(data.orderId); // 流式传输结束
    es.close();
  });

  es.addEventListener('error', (e) => {
    if(e.data) {
      const data = JSON.parse(e.data);
      handlers.onError?.(data.message);
      es.close();
    }
  });

  return es;
};

// 支付和详细分析SSE流
export const initiatePayment = (prevOrderId, analysisType, handlers) => {
  const url = `${BASE_URL}/api/orders/${prevOrderId}/payment?type=${analysisType}`;
  const es = createEventSourceWithRecovery(url, handlers, analysisType);

  // 监听支付相关事件
  es.addEventListener('payment', (e) => {
    const data = JSON.parse(e.data);
    handlers.onPaymentQR?.(data.qrcodeUrl, data.orderId); 
    // 保存分析订单ID，方便恢复
    if (data.orderId) {
      localStorage.setItem(`paid_analysis_${analysisType}`, data.orderId);
    }
  });

  // 添加全内容恢复事件
  es.addEventListener('full_content', (e) => {
    try {
      const data = JSON.parse(e.data);
      if (data && data.content) {
        console.log(`恢复完整付费内容: ${analysisType}`);
        handlers.onAnalysisRestore?.(data.content);
        // 标记为已解锁
        handlers.onPaymentstatus?.();
      }
    } catch (error) {
      console.error('解析full_content事件失败:', error);
    }
  });

  // 监听分析内容流式传输
  es.addEventListener('stream', (e) => {
    const data = JSON.parse(e.data);
    handlers.onAnalysisChunk?.(data.content); // 流式传输详细分析
  });

  es.addEventListener('stream_end', () => {
    handlers.onAnalysisEnd?.(); // 详细分析传输结束
    es.close();
  });

  es.addEventListener('error', (e) => {
    if(e.data) {
      const data = JSON.parse(e.data);
      handlers.onError?.(data.message);
      es.close();
    }
  });

  return es;
};