import React from 'react';
import '../App.css';

const ReconnectingStatus = ({ analysisType }) => {
  return (
    <div className="reconnecting-overlay">
      <div className="reconnecting-message">
        <h3>正在恢复{analysisType}内容...</h3>
        <p>请稍候，已购买的内容将很快恢复显示</p>
      </div>
    </div>
  );
};

export default ReconnectingStatus;
